::-webkit-scrollbar {
    width: 10px;
}

/* 背景 */
::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 5px;
}

/* 滑块 */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px;
}

/* 滑块悬停状态 */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}